.widget-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.widget-toolbar {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  font-size: 16px;
  font-weight: 500;

  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }
}

.widget-content {
  flex: 1;
  padding: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  overflow-y: auto;
}

.chart-container {
  width: 100%;
  max-width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.legend-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 12px;
  background: rgba(102, 126, 234, 0.05);
  border-radius: 8px;
  transition: all 0.2s ease;

  &:hover {
    background: rgba(102, 126, 234, 0.1);
    transform: translateX(4px);
  }

  .legend-color {
    width: 16px;
    height: 16px;
    border-radius: 4px;
    flex-shrink: 0;
  }

  .legend-label {
    flex: 1;
    font-size: 14px;
    font-weight: 500;
    color: var(--text-primary);
  }

  .legend-count {
    font-size: 14px;
    font-weight: 600;
    color: var(--text-primary);
    min-width: 30px;
    text-align: right;
  }

  .legend-ratio {
    font-size: 13px;
    color: var(--text-secondary);
    min-width: 45px;
    text-align: right;
  }
}

@media (max-width: 768px) {
  .chart-container {
    max-width: 250px;
    height: 250px;
  }
}
